import { MODULE_ID } from '@utils/module'

export function registerWrapper(target: string, fn: libWrapper.OverrideFunction, type: 'OVERRIDE'): number
export function registerWrapper(target: string, fn: libWrapper.WrappedFunction, type: 'WRAPPER' | 'MIXED'): number
export function registerWrapper(
    target: string,
    fn: libWrapper.WrappedFunction | libWrapper.OverrideFunction,
    type: 'WRAPPER' | 'MIXED' | 'OVERRIDE' = 'WRAPPER'
): number {
    return libWrapper.register(MODULE_ID, target, fn, type)
}
